.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgrey;
}

.sidebar{
    flex: 0.35;
    flex-direction: column;
    display: flex;
}
.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content : space-between;
    min-width: 10vw;
}

.sidebar_headerRight> .MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px  !important;
}

.sidebar_searchContainer > input{
    border: none;
    margin-left: 10px;
}

.sidebar_searchContainer> .MuiSvgIcon-root{
color: grey;
padding: 10px;
}

.sidebar_chats{
    background-color:white;
    flex: 1;
    overflow: scroll;
}